.ReactTable .rt-thead.-header {
  box-shadow: none;
  border-bottom: 1px solid #d8d9d9;
}

.ReactTable .rt-tbody .rt-tr-group,
.ReactTable .rt-tbody .rt-tr-group:last-child {
  border-bottom: 1px solid #d8d9d9;
}

.ReactTable .rt-thead .rt-th {
  padding: 0 27px 17px 27px;
  text-align: left;
  border-right: 0;
  font-size: 14px;
}

.ReactTable .rt-td {
  padding: 12px 27px;
  font-size: 14px;
}

.ReactTable .rt-tbody .rt-td {
  border-right: 0;
}

.ReactTable {
  border: 0;
  color: #b3b3b3;
}

.ReactTable .rt-thead .rt-th.-sort-asc,
.ReactTable .rt-thead .rt-th.-sort-desc {
  box-shadow: none;
  outline: 0 !important;
}

.ReactTable .rt-thead .rt-th:focus {
  outline: 0 !important;
}

.ReactTable .rt-thead .label::after {
  display: inline-block;
  vertical-align: middle;
  margin: -6px 0 0 4px;
}

.ReactTable .rt-thead .rt-th.-sort-asc .label::after {
  content: "↓";
}

.ReactTable .rt-thead .rt-th.-sort-desc .label::after {
  content: "↑";
}
